import {numeric, required, max_value, min_value} from 'vee-validate/dist/rules';
import {extend, setInteractionMode} from 'vee-validate'
import {isAfter, isValid, parse} from 'date-fns'

setInteractionMode('eager')
extend('logs_expiration_date', value => {
  const parsed = parse(value, 'yyyy-MM-dd', new Date())
  if (!isValid(parsed))
    return 'Format should be YYYY-MM-DD'
  if (!isAfter(parsed, new Date()))
    return 'Invalid expiration date'
  return true
})

extend('required', {
  ...required,
  message: '{_field_} cannot be empty',
})

extend('numeric', {
  ...numeric,
  message: 'Only numbers are allowed'
})

extend('max_value', {
  ...max_value,
  message: 'Should not exceed {max}'
})

extend('min_value', {
  ...min_value,
  message: 'Should be greater than {min}'
})
