<template>
  <v-app>
    <v-navigation-drawer app v-model="navOpen" color="primary">
      <AdminNavigationDrawer v-if="isAdmin"/>
      <UserNavigationDrawer v-if="isUser" />
      <CsmNavigationDrawer v-if="isCsm" />
    </v-navigation-drawer>
    <v-app-bar
      app
      color="primary"
      dark
    >
      <v-app-bar-nav-icon v-if="isLoggedIn" @click="isNavOpen = !isNavOpen" />
      <v-toolbar-title>
        AWBC-19VIDMS
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <AppBarAvatarMenu v-if="isLoggedIn" />

      <v-btn
        @click="logout"
        v-if="isLoggedIn"
        icon
      >
        <v-icon>mdi-logout</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <router-view/>
    </v-main>
  </v-app>
</template>

<script>
import AdminNavigationDrawer from './components/admin/NavigationDrawer.vue'
import AppBarAvatarMenu from './components/AppBarAvatarMenu.vue';
import UserNavigationDrawer from './components/user/NavigationDrawer.vue'
import CsmNavigationDrawer from './components/csm/NavigationDrawer.vue'

export default {
  name: 'App',
  components: {
    AdminNavigationDrawer,
    UserNavigationDrawer,
    CsmNavigationDrawer,
    AppBarAvatarMenu,
  },
  data: () => ({
    isNavOpen: false,
  }),


  methods: {
    logout() {
      let location
      if (this.isAdmin) location = '/admin/login'
      else if (this.isCsm) location = '/csm/login'
      else location = '/user/login'

      console.log('location', location);

      localStorage.removeItem('dms-token')
      this.isNavOpen = false

      this.$router.push(location).catch()
    }
  },

  computed: {
    isAdmin () {
      return this.$route.meta.adminLoggedIn
    },

    isUser () {
      return this.$route.meta.userLoggedIn
    },

    isCsm () {
      return this.$route.meta.csmLoggedIn
    },

    isLoggedIn () {
      const {meta} = this.$route
      return meta.adminLoggedIn || meta.userLoggedIn || meta.csmLoggedIn
    },

    navOpen: {
      get () {
        return this.isNavOpen && this.isLoggedIn
      },
      set (value) {
        this.isNavOpen = value
      }
    }
  },
};
</script>
