import jwt from 'jsonwebtoken'

const adminLoggedInGuard = (to, from, next) => {
  const token = localStorage.getItem('dms-token')
  const secret = process.env.VUE_APP_API_SECRET

  if (!token) {
    next('/admin/login')
    return
  }

  try {
    const decoded = jwt.verify(token, secret, {subject: 'login'})
    if (decoded.data.type === 'admin') {
      next()
      return
    } else if (decoded.data.type === 'team_lead') {
      next('/users/dashboard')
      return
    }

    throw new Error('invalid token');
  } catch {
    localStorage.removeItem('dms-token')
    next('/admin/login')
  }
}

