const csmLoggedInGuard = (to, from, next) => {
  const token = localStorage.getItem('dms-token')
  const secret = process.env.VUE_APP_API_SECRET

  if (!token) {
    next('/user/login')
    return
  }

  try {
    const decoded = jwt.verify(token, secret, {subject: 'login'})
    if (decoded.data.type === 'cs_manager') {
      next()
      return
    } else if (decoded.data.type === 'admin') {
      next('/admin/manage-users')
      return
    } else if (decoded.data.type === 'team_lead') {
      next('/users/dashboard')
      return
    }

    throw new Error('invalid token');
  } catch {
    localStorage.removeItem('dms-token')
    next('/csm/login')
  }
}

const guestGuard = (to, from, next) => {
  const token = localStorage.getItem('dms-token')
  const secret = process.env.VUE_APP_API_SECRET

  if (!token) {
    next()
    return
  }

  try {
    const decoded = jwt.verify(token, secret, {subject: 'login'})

    if (decoded.data.type === 'admin') {
      next('/admin/manage-users')
      return
    } else if (decoded.data.type === 'cs_manager') {
      next('/csm/vaccine-logs')
      return
    } else if (decoded.data.type === 'team_lead') {
      next('/user/dashboard')
      return
    }

    throw new Error('invalid token');
  } catch {
    localStorage.removeItem('dms-token')
    next()
  }
}

export default {
  adminLoggedInGuard,
  userLoggedInGuard,
  csmLoggedInGuard,
  guestGuard,
}
